import { useState,useEffect } from 'react';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { textFilter } from 'react-bootstrap-table2-filter';
import {data} from './data';

function App() {
	
	const styledHead = (val) => {return {width: val+"em",textAlign:"center"}};	
	
	const columns = [
		{id:"1",dataField: "Distributor", text: "Distributor",
			headerStyle: styledHead(10),
			filter: textFilter(),
			align: "center",
			sort: true
		},
		{id:"2",dataField: "State", text: "State",
			headerStyle: styledHead(10),
			filter: textFilter(),
			align: "center",
			sort: true
		},
		{id:"3",dataField: "ContactNumber", text: "Contact Number",
			headerStyle: styledHead(10),
			filter: textFilter(),
			align: "center",
			sort: true
		},
		{id:"4",dataField: "Email-Id", text: "Email-Id",
			headerStyle: styledHead(10),
			filter: textFilter(),
			align: "center",
			sort: true
		},
		{id:"5",dataField: "Address", text: "Address",
			headerStyle: styledHead(20),
			filter: textFilter(),
			align: "center",
			sort: true
		} 
	];
	
	const CaptionElement = () => <>
		<h3 style={{ borderRadius: '0.25em', textAlign: 'left', color: '#00001a', border: '1px solid #00001a', padding: '0.5em' }}>Remdesivir Distributor Contact Details</h3>
		<ul style={{color: 'black'}}>
			<li>
				<i><h6 >Click on column header to sort values and filter as required</h6></i>
			</li>
			<li>
				<i><h6 >Data taken from this <a target="_blank" href="https://lnkd.in/ezvK_G4"> link </a>. I take no responsibility for the correctness of the data.</h6></i>
			</li>
		</ul>
		</>;
	
	return (
    <div className="App">
		<BootstrapTable id="table"
		  keyField="id"
		  data={ data }
		  columns={ columns }
		  striped
		  hover
		  condensed
		  noDataIndication={()=> "LOADING..." }
		  caption={<CaptionElement />}
		  filter={ filterFactory() }
		/>
    </div>
  );
}

export default App;